import './style/LikesGoods.scss';
import { useEffect, useContext, useState } from 'react';
import { Context } from '../..';
import useLoading from '../../Hooks/useLoading';
import ProductTable from '../Shop/Goods/components/ProductTable/ProductTable';
import Title from '../../UI/Title/Title';
import { FavoriteAPI } from '../../http/favoriteAPI';
import GoodsRow from '../../UI/GoodsRow/GoodsRow';
import { LikeTuner } from './LikeTuner';
import { renderSortMenu } from '../../components/Product/addClassSort&Search';
import { observer } from 'mobx-react-lite';
import { LikePagination } from './LikePagination';
import { LoadingWin } from '../../UI/WinLoad/WinLoad';

const LikesGoods: React.FC = observer(() => {
  const { favorite } = useContext(Context);
  const [limit, setLimit] = useState<string>('8');
  const [isLoading, fetching] = useLoading(async () => {
    const response = await FavoriteAPI.getAll(limit);
    favorite.setLimitProduct = response.devices;
  });

  const [isLoadingTwo, fetchingTwo] = useLoading(async () => {
    if (favorite.count != favorite.getProductsLimit.length) {
      const count = favorite.getProductsLimit.length - 1;

      if (count >= 0) {
        const response = await FavoriteAPI.getAll(count.toString());
        favorite.setLimitProduct = response.devices;
      }
    }
  });

  useEffect(() => {
    window.addEventListener('click', renderSortMenu);
    return function cleanup() {
      window.removeEventListener('click', renderSortMenu);
    };
  }, []);

  useEffect(() => {
    fetching();
    document.title = 'Favorite goods';
  }, []);

  useEffect(() => {
    fetchingTwo();
  }, [favorite.count]);

  return (
    <>
      <div className="likesGoods">
        <div className="indentstart"></div>
        <Title name="Favorites" />

        <>
          <GoodsRow classname="like">
            <LikeTuner setLimit={setLimit} limit={limit} />
            <LoadingWin isLoading={isLoading}>
              <ProductTable like={true} />
            </LoadingWin>
            {+favorite.count > +limit &&
              +favorite.count > +favorite.getProductsLimit.length && (
                <LikePagination favorite={favorite} limit={limit} />
              )}
          </GoodsRow>
        </>
      </div>
    </>
  );
});

export default LikesGoods;
