import { observer } from 'mobx-react-lite';
import useLoading from '../../Hooks/useLoading';
import { FavoriteAPI } from '../../http/favoriteAPI';
import Favorite from '../../mobx/Favorite';
import Loading from '../../UI/Loading/Loading';
import './style/LikePagination.scss';

export const LikePagination: React.FC<LikePaginationConf> = observer(
  ({ limit, favorite }) => {
    const [isLoading, fetching] = useLoading(async () => {
      const count = favorite.getProductsLimit.length + +limit;
      const response = await FavoriteAPI.getAll(count.toString());

      favorite.setLimitProduct = response.devices;
    });

    return (
      <div className="likepagination">
        <div className="likepagination__wrap">
          <div className="likepagination__btn" onClick={fetching}>
            {isLoading ? <Loading /> : <p className="">Show more</p>}
          </div>
        </div>
      </div>
    );
  }
);

interface LikePaginationConf {
  limit: string;
  favorite: Favorite;
}
